<template>
  <div class="map-container position-relative">
    <img src="@/assets/images/peta.png" alt="Peta Indonesia" class="w-100" />

    <!-- Pin lokasi -->
    <div
      v-for="(branch, index) in branches"
      :key="index"
      class="map-pin"
      :style="{ top: branch.top, left: branch.left }"
      @click="selectBranch(branch)"
    ></div>

    <!-- Info cabang -->
    <div v-if="selectedBranch" class="branch-info shadow" :style="getPopupPosition(selectedBranch)">
      <button class="close-btn" @click="selectedBranch = null">Ã—</button>
      <h5>{{ selectedBranch.name }}</h5>
      <p>{{ selectedBranch.address }}</p>
      <p><strong>Unit:</strong> {{ selectedBranch.unit }}</p>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from 'vue'

const branches = ref([
  {
    name: 'Balikpapan',
    address: 'Jl. Soekarno Hatta No. 15, Balikpapan, Kalimantan Timur',
    unit: '100',
    top: '44%',
    left: '46%',
  },
  {
    name: 'Berau',
    address: 'Jl. Pulau Derawan No. 8, Berau, Kalimantan Timur',
    unit: '250',
    top: '30%',
    left: '49%',
  },
  {
    name: 'Banjarmasin',
    address: 'Jl. Ahmad Yani No. 25, Banjarmasin, Kalimantan Selatan',
    unit: '199',
    top: '51%',
    left: '44%',
  }
])

const selectedBranch = ref(null)

const selectBranch = (branch) => {
  if (selectedBranch.value === branch) {
    selectedBranch.value = null
  } else {
    selectedBranch.value = branch
  }
}

// Fungsi untuk mendapatkan posisi popup
const getPopupPosition = (branch) => {
  const top = parseFloat(branch.top);
  const left = parseFloat(branch.left);
  const offsetTop = 10; // Jarak atas
  const offsetLeft = 10; // Jarak kiri

  return {
    top: `calc(${top}% + ${offsetTop}px)`,
    left: `calc(${left}% + ${offsetLeft}px)`
  }
}

// Klik luar popup dan pin => tutup
const handleClickOutside = (event) => {
  const infoBox = document.querySelector('.branch-info')
  const isPin = event.target.classList.contains('map-pin')
  const isInsideInfo = infoBox && infoBox.contains(event.target)

  if (!isPin && !isInsideInfo) {
    selectedBranch.value = null
  }
}

onMounted(() => {
  window.addEventListener('click', handleClickOutside)
})

onBeforeUnmount(() => {
  window.removeEventListener('click', handleClickOutside)
})
</script>

<style scoped>
.map-container {
  max-width: 1000px;
  margin: auto;
  position: relative;
}

.map-pin {
  width: 20px;
  height: 20px;
  background-color: red;
  border: 3px solid white;
  border-radius: 50%;
  position: absolute;
  cursor: pointer;
  transform: translate(-50%, -50%);
}

.branch-info {
  position: absolute;
  background: white;
  padding: 16px;
  border-radius: 8px;
  max-width: 300px;
  box-shadow: 0 0 10px rgba(0,0,0,0.2);
}

.close-btn {
  position: absolute;
  top: 6px;
  right: 10px;
  background: transparent;
  border: none;
  font-size: 20px;
  cursor: pointer;
  line-height: 1;
}
</style>
